export default {
	data() {
		return {
			animationData:{},
			animationDataText:{}
		}
	},
	onLoad() {
		this.animation = uni.createAnimation({
			duration: 3000,
			timingFunction: 'ease',
		})
		this.animation.scale(1,1).opacity(1).step()
		this.animationData = this.animation.export()
	},
	onUnload() {
		this.animationData = null
	},
	methods: {
		toPath(){
			uni.switchTab({
				url:"/pages/main/main"
			})
		}
	}
}
